				<view class="tui-modal-custom-text">恭喜你，兑换成功！</view>
				<tui-button height="72rpx" :size="28" type="danger" shape="circle" @click="exchangeCallback" style="background: #FF107F !important;">确定</tui-button>
			</view>
		</tui-modal>
